.form-style {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50%;
}

.first-container {
  display: flex;
  font-weight: bold;
  flex-direction: row;
  margin-bottom: 30px;
  text-transform: uppercase;
  width: 40vw;
  justify-content: space-around;
  margin-top: 40px;
}
.pseudo-container {
  display: flex;
  flex-direction: column;
  border: 1px;
  border-radius: 40px;
  text-align: center;
  padding: 15px;
  background: linear-gradient(90deg, #bba9a9, #af8dca);
  box-shadow: white 10px 2px 10px;
  color: white;
  line-height: 25px;
}

.pseudo-container:hover {
  box-shadow: white 10px 5px 15px;
}


.pseudo-container input {
  border-radius: 20px;
  color: black;
  padding-left: 10px;
}
.category-container {
  border: 1px;
  border-radius: 10px;
  text-align: center;
  padding: 10px;
  border-radius: 40px;
  width: 15vw;
  color: white;
  box-shadow: white 10px 2px 10px;
  background: linear-gradient(90deg, #bba9a9, #af8dca);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.category-container select{
  color: black;
  background: linear-gradient(90deg, #bba9a900, #af8dca00);
  align-items: center;
  text-align: center;
  width: 80%;
}

.category-container:hover {
  box-shadow: white 10px 5px 15px;
}

.second-container {
  display: flex;
  border: 1px;
  flex-direction: column;
  border-radius: 20px;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  line-height: 45px;
  /* text-transform: uppercase; */
  background: linear-gradient(90deg, #b9b0b0, #af8dca);
  padding: 15px;
  font-size: 25px;
  color: white;
}
.second-container textarea {
  border: 1px;
  border-radius: 10px;
  padding: 5px;
  width: 90%;
  height: 70px;
  margin-bottom: 10px;
  color: white;
}
.form-image-container {
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  /* text-transform: uppercase; */
  color: white;
  font-size: 25px;
  background: linear-gradient(90deg, #b9b0b0, #af8dca);
}
.form-image-container input {
  padding-left: 30px;
}
.social-container-form {
  display: flex;
  border: 1px;
  flex-direction: column;
  border-radius: 20px;
  background: white;
  padding-top: 15px;
  padding-bottom: 15px;
  justify-content: center;
  align-items: center;
  background: linear-gradient(90deg, #b9b0b0, #af8dca);
}
.snapchat-container input {
  border-bottom: 1px solid;
}
.facebook-container input {
  border-bottom: 1px solid;
}
.youtube-container input {
  border-bottom: 1px solid;
}
.insteg-container input {
  border-bottom: 1px solid;
}
.twitter-container input {
  border-bottom: 1px solid;
}

.youtube {
  color: #eb3223;
}
.facebook {
  color: #4968ad;
}

.twitter {
  color: #49a1eb;
}

.instagram {
  color: black;
}
.snapchat {
  color: rgb(252, 255, 57);
}

.submit-btn {
  background: linear-gradient(90deg, #e66465, #a556e6);
  color: white;
  padding: 8px;
  border-radius: 20px;
  width: 10vw;
  margin-top: 20px;
  margin-left: 250px;
}
